<template>
  <div class="sls">
    <div class="slsbox">
      <div class="sls-top">
        <span>4</span>
        <span>0</span>
        <span>4</span>
        <p>页面不存在</p>
      </div>
      <div class="but"> 
        <el-button type="primary">返回首页</el-button>
        <el-button type="primary">返回上一页</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: ""
};
</script>

<style lang="scss" scoped>
.sls {
  width: 100%;
  height: 100%;
  background: gainsboro;
  .slsbox {
    margin: 0 auto;
    .sls-top {
      width: 100%;
      text-align: center;
      span {
        font-size: 200px;
      }
      span:nth-of-type(1){
          color: blue
      }
      span:nth-of-type(2){
          color: darkblue
      }
      span:nth-of-type(3){
          color: blue
      }
      p{
          font-size: 40px
      }
    }
    .but{
        width: 100%;
        text-align: center;
        margin: 20px auto;
    }
  }
}
</style>


